import { Component } from '@angular/core';
import { IonicPage, ViewController, AlertController } from 'ionic-angular';
import * as providers from '../../providers';
import * as services from '../../services'

@IonicPage({
  name: 'RegisterPage',
  segment: 'register'
})
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {

  account: any;
  confirmPassword = "";

  constructor(
    private viewCtrl: ViewController,
    public alertCtrl: AlertController,
    private loading: providers.AppLoadingProvider,
    private acountresourceApi: services.AccountresourceApi) {

      this.account = {};
  }

  ngOnInit() {
  }

  doRegister() {
    if (this.account.password !== this.confirmPassword) {
      let alert = this.alertCtrl.create({
        title: 'エラー',
        subTitle: 'パスワードが一致しません。',
        buttons: ['OK']
      });
      alert.present();
    return;
    }

    this.account.langKey = 'ja';
    this.loading.start(() => {

      return this.acountresourceApi.registerAccountUsingPOSTWithHttpInfo(this.account).toPromise()
      .then(() => {
        
        let alert = this.alertCtrl.create({
          title: '情報',
          subTitle: 'ユーザ登録を受付ました。メールを確認して、ユーザ登録を完了してください。',
          buttons: ['OK']
        });
        alert.present();
        
        this.viewCtrl.dismiss(
          {'result': 'registed', 'registerAccount': this.account});
      }).catch((response) => 
        this.processError(response)
      );
    });
  }

  private processError(response) {
    if (response.status === 400 && response._body === 'login already in use') {

      let alert = this.alertCtrl.create({
        title: 'エラー',
        subTitle: 'ユーザ名は既に使用されています。',
        buttons: ['OK']
      });
      alert.present();

    } else if (response.status === 400 && response._body === 'email address already in use') {

      let alert = this.alertCtrl.create({
        title: 'エラー',
        subTitle: 'メールアドレスは既に使用されています。',
        buttons: ['OK']
      });
      alert.present();

    } else {

      let alert = this.alertCtrl.create({
        title: 'エラー',
        subTitle: '登録に失敗しました。時間をおいてから再度実行してください。',
        buttons: ['OK']
      });
      alert.present();
    }

  }

  dismiss(data?: any) {
    this.viewCtrl.dismiss({'result': 'cancel'});
  }
}



